    --c-white: #ffffff;
    --c-beige: #f5f5dc;
    --c-black: #000000;
    --c-gray: #eaeef0;
    --c-dark-gray: #e5e7eb;
    --c-light-gray: #f5f5f5;
    --c-primary: #25294a;
    --c-primary-light: #323764;
    --c-accent: #4e7cff;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body,
html {
    font-family: "Poppins", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

html {
    font-size: 62.5%;
    background: lightgray;
    overflow-y: scroll;
    /* Prevents page from jumping when there's enough content to scroll */
}

body {
    font-size: 1.6rem;
    font-weight: 300;
    line-height: 1.5;
}

h1,
h2,
h3,
h4,
button,
input,
label {
    line-height: 1.1;
}

img,
picture {
    max-width: 100%;
    display: block;
}

input,
button,
textarea,
select {
    font-family: inherit;
    font-size: inherit;
}

button {
    color: inherit;
    cursor: pointer;
    border: none;
    background: transparent;
}

ul,
li {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: var(--c-black);
}

.btn {
    border-radius: 24px;
    padding: 8px 16px;
    background: var(--c-accent);
    color: var(--c-white);
    transition: all 0.3s ease;
}

.btn:hover {
    color: var(--c-accent);
    opacity: 0.75;
}

.link:hover {
    opacity: 0.75;
    transition: all 0.3s ease;
}

.disabled {
    display: none !important;
}

#page {
    background: var(--c-beige);
    color: var(--c-primary);
    min-height: 100vh;
    width: 100%;
    display: grid;
    place-items: center;
    grid-auto-rows: max-content;
    gap: 20px;
    margin: 0 auto;
}

.header {
    width: 100%;
}

.header-container {
    max-width: 1280px;
    display: flex;
    align-items: center;
    gap: 40px;
    margin: 0 auto;
    padding: 10px 20px;
}

.logo {
    display: flex;
    align-items: center;
    gap: 10px;
}

.logo .title {
    font-size: 2.5rem;
    font-weight: 400;
}

.header-menu nav {
    display: flex;
    align-items: center;
    gap: 20px;
}

.header-menu button.active {
    position: relative;
}

button.active::after {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 40px;
    height: 2px;
    background-color: var(--c-primary);
}

#content {
    padding: 20px;
    width: 100%;
    max-width: 1280px;
    border-radius: 5px;
}

.hero-container .hero-row {
    display: grid;
    grid-template-columns: 40% 1fr;
    gap: 40px;
    /* max-height: 75vh; */
}

.hero-description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
}

.hero-description .hero-pretitle {
    font-size: 1.8rem;
    font-weight: 400;
}

.hero-description .hero-title {
    font-size: 3.2rem;
    font-weight: 700;
}

.hero-description .hero-text {
    font-size: 1.8rem;
    font-weight: 300;
}

.hero-description .hero-call-to-action {
    display: flex;
    align-items: center;
    gap: 20px;
}

.hero-description .hero-call-to-action a {
    padding: 10px 20px;
    border-radius: 24px;
    font-size: 1.4rem;
    font-weight: 700;
}

.hero-description .hero-call-to-action .hero-more-link {
    background: var(--c-accent);
    color: var(--c-white);
}

.hero-description .hero-call-to-action .hero-order-link {
    border: 1px solid var(--c-primary);
    background: var(--c-light-gray);
    color: var(--c-primary);
}

.hero-image-container {
    display: grid;
    place-items: center;
}

.hero-image-container .hero-image {
    width: 100%;
    max-width: 400px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* General Section Styles */

.section {
    padding: 20px;
    background: var(--c-white);
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 768px;
    margin: 0 auto;
}

.section-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--c-primary);
    margin-bottom: 20px;
}

/* Menu Section Styles */

.menu-section {
    max-width: 45ch;
}

.menu-list {
    list-style: none;
    padding: 0;
}

.menu-item {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid var(--c-gray);
}

.item-name {
    font-size: 1.6rem;
    color: var(--c-primary-light);
}

.item-price {
    font-size: 1.6rem;
    color: var(--c-primary);
}

/* About Section Styles */

.about-text {
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-size: 1.8rem;
    font-weight: 300;
    color: var(--c-primary-light);
    text-align: justify;
}

/* Media Queries */

@media (max-width: 768px) {
    .hero-container .hero-row {
        grid-template-columns: 1fr;
    }

    #page {
        padding: 10px;
    }

    #content {
        padding: 10px;
    }

    .header-container {
        flex-direction: column;
        gap: 20px;
    }

    .header-menu nav {
        flex-direction: row;
        gap: 10px;
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;IAClB,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;IACjB,sBAAsB;IACtB,uBAAuB;IACvB,oBAAoB;IACpB,0BAA0B;IAC1B,mBAAmB;AACvB;;AAEA;;;IAGI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA;;IAEI,uLAAuL;AAC3L;;AAEA;IACI,gBAAgB;IAChB,qBAAqB;IACrB,kBAAkB;IAClB,qEAAqE;AACzE;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;;;;;;;IAOI,gBAAgB;AACpB;;AAEA;;IAEI,eAAe;IACf,cAAc;AAClB;;AAEA;;;;IAII,oBAAoB;IACpB,kBAAkB;AACtB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;;IAEI,qBAAqB;AACzB;;AAEA;IACI,qBAAqB;IACrB,qBAAqB;AACzB;;AAEA;IACI,mBAAmB;IACnB,iBAAiB;IACjB,2BAA2B;IAC3B,qBAAqB;IACrB,yBAAyB;AAC7B;;AAEA;IACI,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,0BAA0B;IAC1B,uBAAuB;IACvB,iBAAiB;IACjB,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,2BAA2B;IAC3B,SAAS;IACT,cAAc;AAClB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,YAAY;IACZ,OAAO;IACP,WAAW;IACX,WAAW;IACX,kCAAkC;AACtC;;AAEA;IACI,aAAa;IACb,WAAW;IACX,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,SAAS;IACT,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;IACnB,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,2BAA2B;IAC3B,qBAAqB;AACzB;;AAEA;IACI,kCAAkC;IAClC,+BAA+B;IAC/B,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,mBAAmB;IACnB,wCAAwC;AAC5C;;AAEA,2BAA2B;;AAE3B;IACI,aAAa;IACb,0BAA0B;IAC1B,mBAAmB;IACnB,wCAAwC;IACxC,WAAW;IACX,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA,wBAAwB;;AAExB;IAC